type changeThemesType = {
    type: "CHANGE_THEMES"
    theme: string
}

const initState = {
    theme: "some"
};

type InitStateType = typeof initState

export const themeReducer = (state: InitStateType = initState, action: changeThemesType): InitStateType => { // fix any
    switch (action.type) {
        case "CHANGE_THEMES": {
            return {
                ...state,
                theme: action.theme
            }
        }
        default:
            return state;
    }
};

export const changeThemeC = (theme: string): changeThemesType => {
    return {type: "CHANGE_THEMES", theme}
};