import React, {useState} from "react";
import {requestsAPI} from "./request-api";
import SuperButton from "../h4/common/c2-SuperButton/SuperButton";
import s from "./Request.module.css";

const Request: React.FC = () => {

    const [isSending, setIsSending] = useState<boolean>(false)
    const [isFetching, setIsFetching] = useState<boolean>(false)
    const [message, setMessage] = useState<string>("")

    const requestPost = () => {
        setIsFetching(true)
        requestsAPI.requestPost(isSending)
            .then((res) => {
                console.log({...res})
                console.log(res.data.errorText)
                setMessage(res.data.errorText)
                setIsFetching(false)
            })
            .catch(rej => {
                console.log({...rej})
                console.log(rej.response ? rej.response.data.errorText : rej.message)
                setMessage(rej.response ? rej.response.data.errorText : rej.message)
                setIsFetching(false)
            })
    }

    return (
        <div>
            <div className={s.inputSet}>
                <br/> <label className={s.switch}>
                <input type="checkbox" onChange={(e) => {
                    setIsSending(e.currentTarget.checked)
                }} checked={isSending}/>
                <span className={`${s.slider_round} ${s.slider}`}/>
            </label>
            </div>
            <SuperButton onClick={requestPost} disabled={isFetching}>post</SuperButton>
            {message}
        </div>
    )
}

export default Request;