import React, {ChangeEvent, useState} from 'react'
import SuperRange from './common/c7-SuperRange/SuperRange'
import SuperDoubleRange from './common/c8-SuperDoubleRange/SuperDoubleRange'
import s from "./HW11.module.css";
import {TextField} from "@material-ui/core";

function HW11() {
    const [value1, setValue1] = useState<number>(0)
    const [value2, setValue2] = useState<number[]>([0, 100])
    const [step, setStep] = useState<number>(1)
    const [disable, setDisable] = useState<boolean>(false)

    const onChangeRange = (values: number | number[]) => {

        if (Number.isInteger(values)) {
            setValue1(values as number)
            setValue2([values as number, value2[1]])
        }

        if (Array.isArray(values)) {
            setValue2([values[0], values[1]])
            setValue1(values[0])
        }

    }

    const onChangeStep = (e: ChangeEvent<HTMLInputElement>) => {
        setStep(+e.currentTarget.value)
    }
    const onChangeMinValue = (e: ChangeEvent<HTMLInputElement>) => {
        onChangeRange([+e.currentTarget.value, value2[1]])
    }
    const onChangeMaxValue = (e: ChangeEvent<HTMLInputElement>) => {
        onChangeRange([value2[0], +e.currentTarget.value])
    }
    const onClickDisabled = () => {
        setDisable(!disable)
    }

    return (
        <div>
            <hr/>
            homeworks 11
            <div>
                <span>{value1}</span>
                <SuperRange
                    onChangeRange={onChangeRange}
                    value={value1}
                    step={step}
                    disabled={disable}
                    // minValue={defaultValue[0]}
                    // maxValue={defaultValue[1]}
                />
            </div>

            <div>
                <span>{value1}</span>
                <SuperDoubleRange
                    onChangeRange={onChangeRange}
                    value={value2}
                    step={step}
                    disabled={disable}
                />
                <span className={s.value}>{value2[1]}</span>

            </div>
            <div className={s.inputSet}>
                min: <TextField variant={"outlined"}
                                size={"small"}
                                type="number"
                                onChange={onChangeMinValue}
                                value={value2[0]}
                                disabled={disable}/>
            </div>
            <div className={s.inputSet}>
                max: <TextField variant={"outlined"}
                                size={"small"}
                                type="number"
                                onChange={onChangeMaxValue}
                                value={value2[1]}
                                disabled={disable}/>
            </div>
            <div className={s.inputSet}>
                step: <TextField variant={"outlined"}
                                 size={"small"}
                                 type="number"
                                 onChange={onChangeStep}
                                 value={step}
                                 disabled={disable}/>
            </div>
            <div className={s.inputSet}>
                disabled: <br/> <label className={s.switch}>
                <input type="checkbox" onClick={onClickDisabled}/>
                <span className={`${s.slider_round} ${s.slider}`}/>
            </label>
            </div>

            <hr/>
            {/*для личного творчества, могу проверить*/}
            {/*<AlternativeSuperRange/>*/}
            {/*<AlternativeSuperDoubleRange/>*/}
            <hr/>
        </div>
    )
}

export default HW11;
